function Instructions({db}) {   
    return(
    <>
    {db === "beer" && <div id="instructions"> 
        <h2>Intruktioner</h2>

        <p>Uppe till vnster finns en switch fr att vlja mellan l- eller vinrekomendationer. Orange fr l och rd fr vin.</p>
        <p>Skriv namnet p en specifik l, lstil eller producent i skfltet ovan. Resultaten visas automatiskt ju mer du skriver.</p>
        <p>Genom att trycka p ett av lkorten s kommer liknande lsorter att visas p skrmen.</p>
        <p>Uppe i det hgra hrnet s ser du vilken l du jmfr med just nu.</p>
        <p>Hoppas att vi kan hjlpa dig att hitta en ny favorit i ldjungeln!</p></div>}

    {db === "wine" && <div id="instructions">
        <h2>Intruktioner</h2>

        <p>Uppe till vnster finns en switch fr att vlja mellan l- eller vinrekomendationer. Orange fr l och rd fr vin.</p>
        <p>Skriv namnet p ett specifikt vin, en vinstil eller producent i skfltet ovan. Resultaten visas automatiskt ju mer du skriver.</p>
        <p>Genom att trycka p ett av vinkorten s kommer liknande vinsorter att visas p skrmen.</p>
        <p>Uppe i det hgra hrnet s ser du vilket vin du jmfr med just nu.</p>
        <p>Hoppas att vi kan hjlpa dig att hitta ett nytt favoritvin!</p></div>}
    </>
    )
}

export default Instructions;